import React from 'react';
import somann from '../assets/somann.png';

const JanGeorg2 = () => {
  return (
    <div className="jangeorg-blokk article-block">
      <h1>Birgitte lager ny versjon av Nesbøs Snømannen</h1>
      
      <img alt='' src={somann} />

      <p className='made-by'>Laget av JanGeorg Lehmann-Røed</p>
    </div>
  );
}

export default JanGeorg2;


